<template>
  <main>
    <p>There are {{totUploaders}} uploaders that send you files</p>
    <router-link
      :to="{path:`/Uploader/${uploader.username}`}"
      v-for="(uploader, index) in uploaders"
      v-bind:key="index"
      @click="setCurrentUploader(index)"
      class="router-link"
    >
      <UploaderItem v-bind:uploader="uploader" />
    </router-link>
  </main>
</template>

<script>
import UploaderItem from "@/components/UploaderItem";

export default {
  name: "Home",
  components: {
    UploaderItem,
  },
  props: ["uploaders"],
  computed: {
    totUploaders: function () {
      return this.uploaders.length;
    },
  },
};
</script>

