<template>
  <article>
    <article>
      <em>Uploader {{uploader.username}}</em>
      have sent {{unreadFiles}} new files to you!
    </article>
    <img v-bind:src="this.logo" alt="logo_uploader" />
  </article>
</template>

<script>
export default {
  props: ["uploader"],
  computed: {
    unreadFiles: function () {
      let numberUnreadFiles = 0;
      if (this.uploader.files !== undefined) {
        this.uploader.files.forEach((element) => {
          if (element.seen === "") numberUnreadFiles++;
        });
        return numberUnreadFiles;
      } else{
        return 0;
      }
    },
    logo: function(){
      if(this.uploader.logo===''){
        return "/default_profile_picture.png";
      } else{
        return `data:image/jpeg;base64, ${this.uploader.logo}`
      }
    }
  },
};
</script>

<style scoped>
article {
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row-reverse;
}

article article {
  flex-basis: 92%;
  flex-grow: 1;
  display: block;
  padding-right: 5px;
  padding-left: 5px;
}

article article {
  margin: 0 2vw 0 2vw;
  padding: 0;
  flex-basis: 90%;
  font-size: 3vh;
}

article article em {
  font-size: 6vh;
  font-style: normal;
  font-family: "Mitr";
  display: block;
}

article img {
  flex-basis: 14vh;
  flex-grow: 0;
  flex-shrink: 0;
  height: 14vh;
  border: 3px solid black;
  border-radius: 200px;
}
</style>