<template>
  <section>
    <div>
      <input type="text" v-model="hashtag" placeholder="filter by hashtag..." />
    </div>
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Hashtags</th>
          <th>Uploaded</th>
          <th>Seen</th>
          <th>Download</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(file, index) in filesFilter" v-bind:key="index">
          <td>{{file.name}}</td>
          <td>{{file.hashtags.join(", ")}}</td>
          <td>{{file.uploaded}}</td>
          <td v-if="file.seen!==''">{{file.seen}}</td>
          <td v-else>unseen file</td>
          <td>
            <a v-bind:href="file.link">
              <img src="download_icon.png" alt="download" />
            </a>
          </td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<script>
export default {
  props: ["files"],
  data: function () {
    return {
      hashtag: "",
    };
  },
  computed: {
    filesFilter: function () {
      if (this.hashtag !== "") {
        return this.files.filter((element) =>
          element.hashtags.includes(this.hashtag)
        );
      } else {
        return this.files;
      }
    },
  },
};
</script>

<style scoped>
section div {
  display: flex;
  width: 95.5%;
  margin-right: auto;
  margin-left: auto;
  flex-direction: row-reverse;
}

section div input[type="text"] {
  margin-right: 0.4vw;
  height: 4vh;
}

section table {
  width: 95%;
  margin-right: auto;
  margin-left: auto;
}

section table thead {
  width: 100%;
  display: block;
  height: 9vh;
}

section table thead tr {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  width: 100%;
  height: 100%;
}

section table thead tr th {
  flex-basis: 20%;
  border-top: 1px solid whitesmoke;
  border-right: 1px solid whitesmoke;
  border-bottom: 1px solid whitesmoke;
  background-color: rgb(63, 63, 63);
  color: white;
  padding-top: 2vh;
  font-size: 3vh;
}
section table thead tr th:last-child {
  border-right: 1px solid rgb(63, 63, 63);
}

section table tbody {
  display: block;
  overflow: auto;
  max-height: 58vh;
  font-size: 2.5vh;
  border: 1px solid grey;
}

section table tbody tr {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  width: 100%;
  height: 6vh;
}

section table tbody tr td {
  flex-basis: 20%;
  padding-top: 1.5vh;
}

section table tbody tr:nth-child(odd) {
  background-color: rgb(230, 230, 230);
}

section table tbody tr td:nth-child(1) {
  padding-left: 1%;
  flex-basis: 19%;
}

section table tbody tr td:nth-child(2),
section table tbody tr td:nth-child(3),
section table tbody tr td:nth-child(4),
section table tbody tr td:last-child {
  text-align: center;
}

section table tbody tr td:last-child {
  padding: 0.5vh;
}

section table tbody tr td a {
  height: 5vh;
  display: block;
}

section table tbody tr td a img {
  height: 100%;
}

::-webkit-scrollbar {
  display: none;
}
</style>