<template>
  <main>
    <h1>Private Area</h1>
    <p>
      Dear {{info.username}}, these are the information about you.
      Here you can change your name, surname, email and password.
      To confirm your changes simply click on 'confirm changes'.
      <em
        v-if="info.totUnseenFiles===0"
      >All files have been read.</em>
      <em v-else>There are files you don't have downloaded yet</em>
    </p>
    <table v-if="info!=={}">
      <colgroup>
        <col />
        <col />
      </colgroup>
      <tr>
        <th>Username</th>
        <td>{{info.username}}</td>
      </tr>
      <tr>
        <th>Name, Surname</th>
        <td>
          <input
            type="text"
            v-model="info.nameSurname.value"
            v-bind:class="[info.nameSurname.active ? 'active': 'inactive']"
            @click="setWritable(info.nameSurname)"
            @keyup.enter="setReadOnly(info.nameSurname)"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Email</th>
        <td>
          <input
            type="text"
            v-model="info.email.value"
            v-bind:class="[info.email.active ? 'active': 'inactive']"
            @click="setWritable(info.email)"
            @keyup.enter="setReadOnly(info.email)"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Password</th>
        <td>
          <input
            type="password"
            v-model="info.password.value"
            v-bind:class="[info.password.active ? 'active': 'inactive']"
            @click="[showPassword(), setWritable(info.password)]"
            @keyup.enter="[hidePassword(), setReadOnly(info.password)]"
            readonly
          />
        </td>
      </tr>
      <tr>
        <th>Total Uploaders</th>
        <td>{{info.totUploaders}}</td>
      </tr>
      <tr>
        <th>Total Files</th>
        <td>{{info.totFiles}}</td>
      </tr>
      <tr>
        <th>Total Unseen Files</th>
        <td>{{info.totUnseenFiles}}</td>
      </tr>
      <tr>
        <th>Total Seen Files</th>
        <td>{{info.totSeenFiles}}</td>
      </tr>
      <tfoot>
        <tr>
          <td></td>
          <td style="text-align: right">
            <button @click="confirmChanges()">CONFIRM CHANGES</button>
          </td>
        </tr>
      </tfoot>
    </table>
  </main>
</template>

<script>
import Axios from "axios";

export default {
  name: "PrivateArea",
  data: function () {
    return {
      info: {
        username: "loading...",
        email: { value: "loading...", active: false },
        nameSurname: { value: "loading...", active: false },
        password: { value: "", active: false },
        totFiles: "loading...",
        totSeenFiles: "loading...",
        totUnseenFiles: "loading...",
        totUploaders: "loading...",
      },
    };
  },
  created: function () {
    Axios.get(`${process.env.VUE_APP_APIROOT}consumerarea/info`)
      .then((response) => {
        this.info.username = response.data.username;
        this.info.email = { value: response.data.email, active: false };
        this.info.nameSurname = {
          value: response.data.nameSurname,
          active: false,
        };
        this.info.password = { value: response.data.password, active: false };
        this.info.totFiles = response.data.totFiles;
        this.info.totSeenFiles = response.data.totSeenFiles;
        this.info.totUnseenFiles = response.data.totUnseenFiles;
        this.info.totUploaders = response.data.totUploaders;
      })
      .catch((error) => console.log(error));
  },
  methods: {
    showPassword: function () {
      event.target.type = "text";
    },
    hidePassword: function () {
      event.target.type = "password";
    },
    setReadOnly: function (property) {
      event.target.readOnly = true;
      property.active = false;
    },
    setWritable: function (property) {
      event.target.readOnly = false;
      property.active = true;
    },
    confirmChanges: function () {
      let changes = {
        username: this.info.username,
        nomeCognome: this.info.nameSurname.value,
        email: this.info.email.value,
        password: this.info.password.value,
      };
      Axios.put(`${process.env.VUE_APP_APIROOT}consumerarea`, changes)
        .then((response) => console.log(response.status))
        .catch((error) => console.log(error));
    },
  },
};
</script>

<style scoped>
h1 {
  text-align: left;
}
span {
  font-style: italic;
}

table {
  margin-left: auto;
  margin-right: auto;
  /* border: 1px dotted black; */
}

table colgroup col {
  width: 200px;
}

table tr,
table tfoot tr {
  height: 5vh;
  min-height: 100%;
}

table tr th {
  text-align: left;
  padding-left: 1vw;
}

table tr td input[type="text"],
table tr td input[type="password"]:focus {
  outline: none;
}

.inactive {
  background-color: inherit;
  border: 2px solid whitesmoke;
}
.active {
  border: 2px solid black;
  border-radius: 10px;
}

table tfoot tr td button {
  box-sizing: border-box;
  height: 4.5vh;
  min-height: 100%;
  display: block;
  margin-right: auto;
  margin-left: auto;
  border-radius: 10px;
  font-family: "Nunito";
  font-weight: bolder;
  background-color: orangered;
  color: white;
}

table tfoot tr td button:focus{
  outline: none;
}
</style>